import React, { FC, useEffect, useMemo, useState } from "react";
import {
  Avatar,
  Box,
  Button,
  Card,
  InputAdornment,
  TextField,
  Typography,
} from "@mui/material";
import { Colors } from "../constants/colors";
import { CoinMarketInfo } from "../types";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { addDays, differenceInDays } from "date-fns";

const COINS = ["ethereum", "usd-coin", "wrapped-bitcoin"];

const digit = new RegExp("^(-?\\d+.?(\\d+)?)$");

const ONE_MONTH = 30;
const THREE_MONTH = 90;
const HALF_YEAR = 180;

const SimpleDeposit: FC = () => {
  const today = useMemo(() => {
    const date = new Date();
    date.setHours(0, 0, 0, 0);
    return date;
  }, []);

  const [coins, setCoins] = useState<CoinMarketInfo[]>([]);
  const [selectedCoin, setSelectedCoin] = useState<CoinMarketInfo | null>(null);
  const [value, setValue] = useState("");
  const [currencyValue, setCurrencyValue] = useState(0);
  const [isValueValid, setValueValid] = useState(true);
  const [selectedDate, setSelectedDate] = useState<Date>(
    addDays(today, ONE_MONTH)
  );
  const [storageDuration, setStorageDuration] = useState(ONE_MONTH);

  useEffect(() => {
    fetch(
      `https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${COINS.join(
        "%2C"
      )}`
    )
      .then((res) => res.json())
      .then((data) => {
        setCoins(data);
      });
  }, []);
  useEffect(() => {
    if (coins.length > 0) {
      setSelectedCoin(coins[0]);
    }
  }, [coins]);
  useEffect(() => {
    if (isValueValid && selectedCoin) {
      const v = parseFloat(value) * selectedCoin.current_price || 0;
      setCurrencyValue(v);
    } else {
      setCurrencyValue(0);
    }
  }, [value, isValueValid, selectedCoin]);

  useEffect(() => {
    setStorageDuration(differenceInDays(selectedDate, today));
  }, [selectedDate, today]);

  const onValueChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (value !== "") {
      if (value.startsWith("-")) {
        setValueValid(false);
      } else {
        setValueValid(digit.test(value));
      }
    } else {
      setValueValid(true);
    }
    setValue(value);
  };

  const handleConnectWallet = () => {
    //TODO
  };

  return (
    <Box sx={{ width: 960, marginLeft: "auto", marginRight: "auto", mb: 2 }}>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          mb: 2,
        }}
      >
        <Typography variant={"h6"} component={"span"} color={Colors.blue100}>
          Deposit now
        </Typography>
        <Typography variant={"h4"} component={"span"}>
          Calculator
        </Typography>
      </Box>
      <Box>
        <Box
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
          }}
        >
          <Box>
            <Card
              variant={"outlined"}
              sx={{
                width: 400,
                padding: 2,
                borderRadius: 4,
                borderColor: Colors.black1,
              }}
            >
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-between",
                }}
              >
                <Typography variant={"h6"} fontWeight={"bold"}>
                  You pay
                </Typography>
                <Button>
                  <Typography color={Colors.blue100}>Max</Typography>
                </Button>
              </Box>
              <Box sx={{ display: "flex", flexDirection: "column" }}>
                <Box sx={{ alignSelf: "flex-end" }}>
                  <TextField
                    type={"text"}
                    error={!isValueValid}
                    value={value}
                    placeholder={"0"}
                    onChange={onValueChange}
                    sx={{ m: 1, width: "25ch", textAlign: "right" }}
                    variant={"standard"}
                    inputProps={{
                      min: 0,
                      style: { textAlign: "right" },
                    }}
                    InputProps={{
                      disableUnderline: true,
                      endAdornment: (
                        <InputAdornment position={"end"}>
                          <Typography
                            sx={{
                              fontSize: 16,
                              fontWeight: "bold",
                            }}
                          >
                            {selectedCoin?.symbol.toUpperCase()}
                          </Typography>
                        </InputAdornment>
                      ),
                    }}
                  />
                </Box>
                <Box sx={{ alignSelf: "flex-end", paddingRight: 2 }}>
                  <Typography>= $ {currencyValue.toFixed(2)}</Typography>
                </Box>
              </Box>
            </Card>
            <Box
              sx={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "flex-start",
                mt: 2,
              }}
            >
              {coins.map((coin) => {
                return (
                  <Button
                    key={`coin-${coin.id}`}
                    component={"span"}
                    sx={{
                      display: "flex",
                      flexDirection: "row",
                      alignItems: "center",
                      border: "1px solid grey",
                      padding: 1,
                      borderRadius: 4,
                      mr: 1,
                    }}
                    onClick={() => setSelectedCoin(coin)}
                  >
                    <Avatar
                      alt={coin.id}
                      src={coin.image}
                      sx={{ marginRight: 1, width: 24, height: 24 }}
                    />
                    <Typography color={Colors.black1}>
                      {coin.symbol.toUpperCase()}
                    </Typography>
                  </Button>
                );
              })}
            </Box>
          </Box>
          <Box>
            <Card
              variant={"outlined"}
              sx={{
                width: 400,
                padding: 2,
                borderRadius: 4,
                borderColor: Colors.black1,
              }}
            >
              <Box>
                <Typography variant={"h6"} fontWeight={"bold"}>
                  Storage duration
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-between",
                  mt: 2,
                }}
              >
                <Button
                  sx={{
                    border: "1px solid grey",
                    borderRadius: 8,
                    borderColor:
                      storageDuration === ONE_MONTH
                        ? Colors.black1
                        : Colors.grey,
                  }}
                  onClick={() => {
                    setStorageDuration(ONE_MONTH);
                    setSelectedDate(addDays(today, ONE_MONTH));
                  }}
                >
                  <Typography
                    fontSize={14}
                    color={
                      storageDuration === ONE_MONTH
                        ? Colors.black1
                        : Colors.grey
                    }
                  >
                    One month
                  </Typography>
                </Button>
                <Button
                  sx={{
                    border: "1px solid grey",
                    borderRadius: 8,
                    borderColor:
                      storageDuration === THREE_MONTH
                        ? Colors.black1
                        : Colors.grey,
                  }}
                  onClick={() => {
                    setStorageDuration(THREE_MONTH);
                    setSelectedDate(addDays(today, THREE_MONTH));
                  }}
                >
                  <Typography
                    fontSize={14}
                    color={
                      storageDuration === THREE_MONTH
                        ? Colors.black1
                        : Colors.grey
                    }
                  >
                    Three months
                  </Typography>
                </Button>
                <Button
                  sx={{
                    border: "1px solid grey",
                    borderRadius: 8,
                    borderColor:
                      storageDuration === HALF_YEAR
                        ? Colors.black1
                        : Colors.grey,
                  }}
                  onClick={() => {
                    setStorageDuration(HALF_YEAR);
                    setSelectedDate(addDays(today, HALF_YEAR));
                  }}
                >
                  <Typography
                    fontSize={14}
                    color={
                      storageDuration === HALF_YEAR
                        ? Colors.black1
                        : Colors.grey
                    }
                  >
                    Half year
                  </Typography>
                </Button>
              </Box>
              <Box sx={{ mt: 1, mb: 2 }}>
                <Typography>Customization</Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  alignItems: "center",
                }}
              >
                <Box sx={{ mr: 2 }}>
                  <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DatePicker
                      disabled
                      todayText={"Today"}
                      label="Start"
                      value={today}
                      onChange={(newValue) => {
                        // handleDateChange(newValue);
                      }}
                      renderInput={(params) => <TextField {...params} />}
                    />
                  </LocalizationProvider>
                </Box>
                <Box>
                  <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DatePicker
                      label="End"
                      value={selectedDate}
                      onChange={(newValue) => {
                        newValue && setSelectedDate(newValue);
                      }}
                      shouldDisableDate={(day) => {
                        if (day) {
                          day.setHours(0, 0, 0, 0);
                          return differenceInDays(day, today) < 30;
                        }
                        return false;
                      }}
                      renderInput={(params) => <TextField {...params} />}
                    />
                  </LocalizationProvider>
                </Box>
              </Box>
            </Card>
          </Box>
        </Box>
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            mt: 2,
          }}
        >
          <Button
            sx={{ backgroundColor: Colors.blue100 }}
            variant={"contained"}
            onClick={handleConnectWallet}
          >
            <Typography>Connect Wallet</Typography>
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default SimpleDeposit;
